import { Component, OnInit } from '@angular/core';
import {AuthService} from '../auth.service';
import { MatSnackBar} from '@angular/material/snack-bar';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css'], 
  
})
export class CartComponent implements OnInit {
  pricefact:any;pricesymbol:any;
  
 getprice(product:any){ console.log( this.pricefact);
  return product.price * this.pricefact;
 
 }
  constructor(private auth :AuthService,private inp:MatInputModule, private snackbar :MatSnackBar) {
     
this.auth.priceFact.subscribe(data=>this.pricefact=data);
this.auth.priceSynbol.subscribe(data=>this.pricesymbol=data);
   
    this.loadCart();}
  isLinear = false;
  ngOnInit(): void {
    
this.auth.priceFact.subscribe(data=>this.pricefact=data);
this.auth.priceSynbol.subscribe(data=>this.pricesymbol=data);
   
  }
  getCartDetails:any=[];
  CartDetails(){
if(localStorage.getItem
  ('localCart')){
    var x =localStorage.getItem
    ('localCart') ;
if(x!=null)    this.getCartDetails = 
JSON.parse
(x);
  }
}

incQnt(ProductId:any, qnt :any){
  for(let i=0; 
  i<this.getCartDetails.length;
  i++){
  if
  (this.getCartDetails[i].productId 
    === ProductId){
  if(qnt != 5)
  this.getCartDetails[i].qnt
   = parseInt(qnt) + 1;
   else this.snackbar.open('Max quantity allowed in one order', 'Ok', {
    duration: 3000
  });
  }
  }
  localStorage.setItem
  ('localCart', 
  JSON.stringify
  (this.getCartDetails));
  this.loadCart();
  }
  
  decQnt(ProductId :any, qnt :any){
  for
  (let i=0; 
  i<this.getCartDetails.length;
   i++){
  if
  (this.getCartDetails[i].productId 
    === ProductId){
  if(qnt != 1)
  this.getCartDetails[i].qnt 
  = parseInt(qnt) - 1;
  }
  }
  localStorage.setItem
  ('localCart', 
  JSON.stringify
  (this.getCartDetails));
  this.loadCart();
  }
  
  total:number =0;
  loadCart(){
  if
  (localStorage.getItem
    ('localCart')){
      var x=localStorage.getItem
      ('localCart');
      if(x!=null) this.getCartDetails = 
 JSON.parse
  (x);
  this.total = 
 this.pricefact * this.getCartDetails.reduce
  (function(acc:any, val :any){
  return acc + (val.price * val.qnt );
  }, 0);
  }
  }
  
  removeall(){
  localStorage.removeItem
  ('localCart');
  this.getCartDetails = [];
  this.total = 0;
  this.cartNumber = 0;
  this.auth.cartSubject.next
  (this.cartNumber);
  }
  
  singleDelete(getCartDetail :any){
  if(localStorage.getItem
    ('localCart')){
      var x= localStorage.getItem
      ('localCart');
  if(x!=null) this.getCartDetails = 
  JSON.parse
  (x);
  for(let i=0; 
  i<this.getCartDetails.length; 
  i++){
  if
  (this.getCartDetails[i].prodId
     === getCartDetail.productId){
  this.getCartDetails.splice
  (i, 1);
  localStorage.setItem
  ('localCart', 
  JSON.stringify
  (this.getCartDetails));
  this.loadCart();
  this.cartNumberFunc();
  }
  }
  }
  }
  
  cartNumber:number = 0;
  cartNumberFunc(){var x=localStorage.getItem
    ('localCart');
    if(x!=null) var cartValue = 
  JSON.parse
  (x);
  this.cartNumber = 
  cartValue.length;
  this.auth.cartSubject.next
  (this.cartNumber);
  }
}
