import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { NavScrollComponent } from './nav-scroll/nav-scroll.component';
import { CardShopComponent } from './card-shop/card-shop.component';
import { ProductComponent } from './product/product.component';
import {HttpClient, HttpClientModule , HTTP_INTERCEPTORS} from '@angular/common/http';
import { HeaderComponent } from './header/header.component';
import { CartComponent } from './cart/cart.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { PostComponent } from './post/post.component';
import { TranslateLoader } from '@ngx-translate/core';
import { TranslateModule } from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { SliderComponent } from './slider/slider.component';
import{MatBadgeModule} from '@angular/material/badge';
import {MatStepperModule} from '@angular/material/stepper'
import{MatSidenavModule} from '@angular/material/sidenav';
import {MatRippleModule} from '@angular/material/core';
import { MatSnackBar, MatSnackBarModule} from '@angular/material/snack-bar';
import { TextComponent } from './text/text.component';
import { FooterComponent } from './footer/footer.component';
import { DealComponent } from './deal/deal.component';
import { FirstpageComponent } from './firstpage/firstpage.component';
import { BlogComponent } from './blog/blog.component';
import { FavComponent } from './fav/fav.component';
import { DialogElementsExampleDialogComponent } from './dialog-elements-example-dialog/dialog-elements-example-dialog.component'
import { MatDialog, MatDialogModule } from '@angular/material/dialog';
import { TeamComponent } from './team/team.component';
import { STEPPER_GLOBAL_OPTIONS } from '@angular/cdk/stepper';
import { FormBuilder } from '@angular/forms';
import {MatMenuModule} from '@angular/material/menu';
import {MatInputModule} from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
const routes: Routes = [{ path: 'first-component', component: FirstpageComponent  },
{ path: 'cart', component: CartComponent },
{path: '', component: FirstpageComponent },

{path: 'fav', component: FavComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    NavScrollComponent,
    CardShopComponent,
    ProductComponent,
    HeaderComponent,
    CartComponent,
    PostComponent,
    SliderComponent,
    TextComponent,
    FooterComponent,
    DealComponent,
    FirstpageComponent,
    BlogComponent,
    FavComponent,
    DialogElementsExampleDialogComponent,
    TeamComponent
  ],
  imports: [
    BrowserModule,
   
    HttpClientModule,
    MatBadgeModule,MatRippleModule,MatSidenavModule,
    MatSnackBarModule ,
    MatInputModule,
    MatFormFieldModule,
    RouterModule.forRoot(routes),
    TranslateModule.forRoot({
        loader: {
            provide: TranslateLoader,
            useFactory: HttpLoaderFactory,
            deps: [HttpClient]
        }
    }),
    BrowserAnimationsModule,MatDialogModule,MatStepperModule,MatMenuModule
  ],
  providers: [HttpClient],
  
  bootstrap: [AppComponent],
  exports: [RouterModule]
})
export class AppModule { }
export function HttpLoaderFactory(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http);
}

export function useLanguage(language: string): void {
  this.translate.use(language);}