import { Component, OnInit, APP_ID_RANDOM_PROVIDER } from '@angular/core';
import { AuthService } from '../auth.service';
@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./h.scss']
})
export class SliderComponent implements OnInit {
  desc:string="E";n:number;
  constructor(auth:AuthService) {
    auth.desc.subscribe(data=>this.desc=data);
    
   }
  category:any = ["accessories", "shirts", "shoes","blazer"];product:any;
  ngOnInit(): void {
    fetch("https://shielded-stream-74886.herokuapp.com/"+ this.category[Math.floor(Math.random() * (3 + 1))])
    .then(data => {
       return data.json();
    })
    .then(data =>{this.product=data;
       
    } );
  }
  description(des:string,prod:any){
    if(des=="A") return prod.DescriptionA;
    else  return prod.Description;
 
  }
 
  randProd(x){
    return this.product[x];
  }

}